import React, { useEffect, useState } from 'react';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import { LazyLoadImage } from 'react-lazy-load-image-component';

import { ArtService } from '../services/ArtService';

function GalleryItemDetails(props) {
    const { id } = props.match.params;
    const [galleryItem, setGalleryItem] = useState();
    const [isMounting, setIsMounting] = useState(false);
    
    const formatUrl = () => {
        const imgUrl = galleryItem.imgUrl.replace('/720/405', '/1200/720').substring(10, 39);
        let formatedUrl = 'https://';
        if (imgUrl.charAt(28) === '.') {
            formatedUrl += imgUrl.substring(0, 28);
        } else if (imgUrl.charAt(28) === '2') {
            formatedUrl += imgUrl + '0';
        } else {
            formatedUrl += imgUrl;
        }
        console.log(formatedUrl.substring(formatedUrl.length - 3))
        return formatedUrl;
    }
    
    const imgHeightHandler = () => {
        return (window.innerWidth < 460) ? 236.39 : 'auto';
    }

    const goToNextItem = (diff) => {
        const id = ArtService.getNextId(galleryItem.id, diff);
        props.history.push(`/gallery/${id}`);
    }

    useEffect(() => {
        setGalleryItem(ArtService.getItemById(id));
        setIsMounting(!isMounting);
        window.addEventListener('resize', imgHeightHandler);
        return (() => setIsMounting(!isMounting))
    }, [id]);

    return (
        <div className="flex column full rtl w100 main-container gallery-item-container">
            <>
                <div className="gallery-item-subcontainer">
                    <div className="flex gallery-item-buttons">
                        <button className="gellery-item-btn" onClick={() => goToNextItem(-1)}>הקודם</button>
                        <button className="gellery-item-btn" onClick={() => goToNextItem(1)}>הבא</button>
                        <button className="gellery-item-btn" onClick={() => props.history.push('/gallery')}>חזרה לגלריה</button>
                    </div>
                    <SwitchTransition mode="out-in">
                        <CSSTransition
                            key={isMounting}
                            timeout={1500}
                            classNames={'gallery-item-animation'}
                            addEndListener={(node, done) => {
                                node.addEventListener("transitionend", done, false);
                            }}
                            unmountOnExit
                        >
                            <>
                                {galleryItem &&
                                    <div>
                                        <LazyLoadImage
                                            alt=""
                                            effect="blur"
                                            height={imgHeightHandler()}
                                            placeholderSrc={galleryItem.imgUrl}
                                            src={formatUrl()}
                                            visibleByDefault={true}
                                        />
                                        <div className="item-field">אמן : {galleryItem.author}</div>
                                        <div className="item-field">קצת על היצירה: {galleryItem.description}</div>
                                    </div>
                                }
                            </>
                        </CSSTransition>
                    </SwitchTransition>
                </div>
            </>
        </div>

    )
}

export { GalleryItemDetails }