import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import { NavBarMobileBtn } from './NavBar/NavBarMobileBtn';

function NavBar() {

    const [isMenuShown, setMenuShown] = useState(false);
    window.addEventListener('resize', () => {
        if (window.innerWidth > 460) setMenuShown(false);
    });

    const menuHandler = () => {
        if (window.innerWidth <= 460) setMenuShown(false);
    }

    const mobileMenuHandler = () => {
        setMenuShown(!isMenuShown)
    }

    return (
        <div className="main-container w100 rtl nav-bar-container">
            <div className="flex main-container nav-bar-subcontainer ">
                <div className="logo" onClick={menuHandler}>
                    <Link to={'/'}>התערוכה שלי</Link>
                </div>
                <div className={`flex nav-bar-menu${isMenuShown ? ' mobile-menu' : ''}`}>
                    <NavBarMobileBtn
                        mobileMenuHandler={mobileMenuHandler}
                        route='/about'
                        txt='מי אנחנו'
                    />
                    <NavBarMobileBtn
                        mobileMenuHandler={mobileMenuHandler}
                        route='/gallery'
                        txt='גלריה'
                    />
                    <NavBarMobileBtn
                        mobileMenuHandler={mobileMenuHandler}
                        route='/contact'
                        txt='צור קשר'
                    />
                </div>
                <div className={`menu-btn${(isMenuShown) ? ' open' : ''}`} onClick={mobileMenuHandler}>
                    <div className="menu-btn__burger">
                    </div>
                </div>
            </div>
        </div>
    )
}

export { NavBar }