import React from 'react';
import { Link } from 'react-router-dom';

function EmptyPageTemplate({ img, txt }) {
    return (
        <div className="flex center align-center full main-container w100 rtl empty-page-container">
            <img className="empty-page-img" src={img} alt={txt} />
            <div className="flex column center align-center">
                <div className="empty-page-txt">{txt}</div>
                <Link className="" to={'/'}>
                    <button>לעמוד הבית</button>
                </Link>
            </div>
        </div>
    )
}

export { EmptyPageTemplate }