import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import { GalleryList } from '../cmps/Gallery/GalleryList';
import { Loading } from '../cmps/Loading';

import { ArtService } from '../services/ArtService';

function Gallery() {
    const [imgs, setImgs] = useState();
    const [isMounting, setIsMounting] = useState(false);

    useEffect(() => {
        setTimeout(() => setImgs(ArtService.query()), 1000);
        setIsMounting(!isMounting);
        return (() => setIsMounting(!isMounting))
    }, [])

    return (
        <SwitchTransition mode="out-in">
            <CSSTransition
                key={isMounting}
                timeout={600}
                classNames={'cmp-animation'}

            >
                <div className="flex column full rtl gallery-container">
                    {imgs &&
                        <>
                            <div className="flex wrap main-container">
                                <GalleryList imgs={imgs} />
                            </div>
                            <Link className="flex main-container w100 home-button-container" to={'/'}>
                                <button>לעמוד הבית</button>
                            </Link>
                        </>}
                    {!imgs && <Loading />}
                </div>
            </CSSTransition>
        </SwitchTransition>
    )
}

export { Gallery };