import { Utils } from './utils';

export const ArtService = {
    query,
    getRandomPics,
    getItemById,
    getNextId
}

function query() {
    Utils.store('ArtsDB', gArts);
    return gArts;
}

function getRandomPics() {
    const arts = JSON.parse(JSON.stringify(gArts));
    const randomArts = [];
    for (let i = 0; i < 4; i++) {
        const randomIdx = Math.floor(Math.random() * (arts.length - 1));
        randomArts[i] = arts[randomIdx];
        arts.splice(randomIdx, 1);
    }
    return randomArts;
}

function getItemById(id) {
    return gArts.find(art => art.id === id);
}

function getNextId(id, diff) {
    const idx = gArts.findIndex(art => art.id === id) + diff;
    if (idx === gArts.length) return gArts[0].id;
    if (idx < 0) return gArts[gArts.length - 1].id;
    return gArts[idx].id;
}

