import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import { Loading } from '../cmps/Loading';
import { Slideshow } from '../cmps/Home/Slideshow';

import { ArtService } from '../services/ArtService';

function Home() {

    const [pics, setPics] = useState();
    const [isMounting, setIsMounting] = useState(false);

    useEffect(() => {
        setPics(ArtService.getRandomPics());
        setIsMounting(!isMounting);
        return (() => setIsMounting(!isMounting))
    }, []);

    return (
        <SwitchTransition mode="out-in">
            <CSSTransition
                key={isMounting}
                timeout={600}
                classNames={'cmp-animation'}
                unmountOnExit
            >
                <div className="flex full main-container w100 rtl home-container">
                    <>
                        {pics &&
                            <>
                                <div className="flex slideshow-container">
                                    <Slideshow pics={pics} />
                                </div>
                                <div className="flex column center action-container">
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Culpa eum sit sint nobis corporis. A vero quod labore optio maiores, numquam aut magni amet error.</p>
                                    <Link className="gallery-btn" to={'/gallery'}>
                                        <button className="home-action-button">לתערוכה</button>
                                    </Link>
                                </div>
                            </>
                        }
                        {!pics && <Loading />}
                    </>
                </div>
            </CSSTransition>
        </SwitchTransition>
    )
}

export { Home }