import React from 'react';
import { Link } from 'react-router-dom';

function GalleryPreview({ img }) {

    const desc = img.description.substring(0, 15).concat('...');

    return (
        <Link to={`/gallery/${img.id}`} className="flex column gallery-preview-container">
            <img src={img.imgUrl} alt={desc} />
            <div className="flex center align-center gallery-preview-container-screen">{img.author}</div>
        </Link>
    )
}

export { GalleryPreview };